

export const current = (state = {}, action) => {
    switch(action.type) {
        case 'CHANGE_CURRENT':
            return action.song;
        default:
            return state;
    }
}

export const playList = ( state = [], action) => {
    let newList = state.concat([]);
    switch(action.type) {
        case 'CHANGE_LIST':
            return action.list;
        case 'ADD_TO_LIST':
            newList.push(action.song)
            return newList;
        default:
            return state;
    }
}

export const index = (state = -1, action) => {
    switch(action.type) {
        case 'CHANGE_INDEX':
            return action.index;
        default:
            return state;
    }
}



