

import { connect } from 'react-redux';
import PlayControl from '../component/playControl';
import { changeStatus, togglePlayList } from '../action/playControl';
import { changeCurrent, changeIndex } from '../action/playList';


const mapStateToProps = (state) => {
    return {
        current: state.current,
        status: state.status,
        showPlayList: state.showPlayList,
        index: state.index
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        changeStatus: (status) => dispatch(changeStatus(status)),
        togglePlayList: (showPlayList) => dispatch(togglePlayList(showPlayList)),
        changeCurrent: (song) =>  dispatch(changeCurrent(song)),
        changeIndex: (index) => dispatch(changeIndex(index))
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(PlayControl);


