

import React, { Component } from 'react';
import TextField from 'material-ui/TextField';
import FontIcon from 'material-ui/FontIcon';
import {red500, greenA200} from 'material-ui/styles/colors';
import Snackbar from 'material-ui/Snackbar';
import IconButton from 'material-ui/IconButton';

let thread = null;

export default class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            len: 0
        };
    }

    handleRequestClose() {
        this.setState({
            open: false,
        });
    };

    handleSearch() {
        let url = `http://mobilecdn.kugou.com/api/v3/search/song?format=jsonp&keyword=${this.props.searchText}&page=1&pagesize=30&showtype=1&callback=kgJSONP238513750<span style="white-space:pre"></span>`;
        var message = {
            type: 'fetch_list_by_search',
            url: url
        };
        window.postMessage(JSON.stringify(message));
    }

    componentDidMount() {
        window.document.addEventListener('message',  (e) => {
            const message = e.data;
            if(JSON.parse(message).type === 'fetch_list_by_search') {
                let list = [];
                JSON.parse(JSON.parse(message).data).forEach( (v, k) => {
                    let song = {
                        Hash: v.hash,
                        FileName: v.filename
                    };
                    list.push(song);
                });
                this.setState({
                    len: list.length,
                    open: true
                });
                this.props.changeSearchList(list);
                this.props.toggleSearchList(this.props.showSearchList);
            }
        });
    }

    getInput(e) {
        this.props.changeSearchText(e.target.value);
    }

    render() {
        return (
            <header style={styles.header}>
                <span style={styles.title}>
                    <FontIcon 
                        className="fa fa-assistive-listening-systems"
                        style={styles.icon}
                        hoverColor={greenA200}
                    >
                    </FontIcon>
                    <span style={styles.name}>
                        爱听
                    </span>
                </span>
                <span style={styles.search}>
                    <TextField 
                        style={styles.input}
                        value={this.props.searchText}
                        hintText="搜索歌曲"
                        onChange={this.getInput.bind(this)}
                    />
                    <IconButton 
                        iconStyle={styles.searchIcon} 
                        iconClassName="fa fa-search"
                        onTouchTap={this.handleSearch.bind(this)}
                    ></IconButton>
                </span>
                <Snackbar
                    open={this.state.open}
                    message={'搜索结果: ' + this.state.len}
                    autoHideDuration={4000}
                    onRequestClose={this.handleRequestClose.bind(this)}
                />
                <audio src={this.props.current.play_url}></audio>
            </header>
        )
    }
}

const styles = {
    header: {
        height: 50,
        display: 'flex',
        background: '#ff4081',
        justifyContent: 'center',
        alignItems: 'center',
        color: '#fff',
    },
    title: {
        flex: 2,
    },
    icon: {
        color: '#fff',
        marginLeft: 10,
    },
    search: {
        flex: 3,
        display: 'flex'
    },
    name: {
        marginLeft: 5,
        fontFamily: 'cursive',
        fontWeight: 'bold',
        fontSize: 20,
    },
    searchIcon: {
        color: '#fff',
        fontSize: '16px'
    },
    input: {
        width: '70%'
    }
}


