

import { connect } from 'react-redux';
import { changeSearchList, toggleSearchList } from '../action/searchList';
import SearchList from '../component/searchList';
import { addToList } from '../action/playList';

const mapStateToProps = (state) =>{
    return {
        searchList: state.searchList,
        showSearchList: state.showSearchList
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        toggleSearchList: (showSearchList) => dispatch(toggleSearchList(showSearchList)),
        addToList: (song) => dispatch(addToList(song))
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(SearchList);


