



export const status = (state = 0, action) => {
    switch(action.type) {
        case 'CHANGE_STATUS':
            return action.status;
        default:
            return state;
    }
}

export const showPlayList = (state = true, action) => {
    switch(action.type) {
        case 'TOGGLE_PLAY_LIST':
            return !action.showPlayList;
        default:
            return state;
    }
}


