

import { combineReducers } from 'redux';

import { author } from './author';
import { searchText } from './header';
import { status, showPlayList } from './playControl';
import { current, playList, index } from './playList';
import { searchList, showSearchList} from './searchList'

export default combineReducers({
    author,
    searchText,
    current,
    status,
    playList,
    showPlayList,
    index,
    searchList,
    showSearchList
});


