
import { createStore, applyMiddleware } from 'redux';
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import reducer from './reducer/reducer';
import Main from './page/main';
import SongList from './page/songList';
import SongRank from './page/songRank';
import About from './page/about';
import Header from './container/header';
import './style/common.css';
import 'font-awesome/css/font-awesome.css';
import injectTapEventPlugin from 'react-tap-event-plugin';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import {Tabs, Tab} from 'material-ui/Tabs';
import SwipeableViews from 'react-swipeable-views';
import PlayControl from './container/playControl';

import logo from './assert/panda.jpg';
import thunk from 'redux-thunk';
import list from './assert/test.json';
import PlayList from './container/playList';
import SearchList from './container/searchList';

injectTapEventPlugin();

let state = {
    author: 'renhongl',
    searchText: '',
    current: {
        play_url: '',
        img: logo,
        song_name: "未知",
        author_name: "未知"
    },
    status: 1,// 0: playing; 1: paused;
    playList: [],
    showPlayList: false,
    index: -1,
    searchList: [],
    showSearchList: false,
};

let store = createStore(reducer, state, applyMiddleware(thunk));

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            slideIndex: 0,
        };
    }

    handleChange(value) {
        this.setState({
            slideIndex: value,
        });
    };

    render() {
        return (
            <MuiThemeProvider>
                <div style={styles.app}>
                    <Header />
                    <Tabs
                        tabItemContainerStyle={styles.tabs}
                        onChange={this.handleChange.bind(this)}
                        value={this.state.slideIndex}
                    >
                        <Tab label="主页" value={0} />
                        <Tab label="歌单" value={1} />
                        <Tab label="排行榜" value={2} />
                        <Tab label="关于" value={3} />
                    </Tabs>
                    <SwipeableViews
                        style={styles.content}
                        index={this.state.slideIndex}
                        onChangeIndex={this.handleChange.bind(this)}
                        >
                        <div style={styles.slide}>
                            <Main />
                        </div>
                        <div style={styles.slide}>
                            <SongList />
                        </div>
                        <div style={styles.slide}>
                            <SongRank />
                        </div>
                        <div style={styles.slide}>
                            <About />
                        </div>
                    </SwipeableViews>
                    <PlayControl />
                    <PlayList />
                    <SearchList />
                </div>
            </MuiThemeProvider>
        )
    }
}

const styles = {
    app: {
        background: '#fff',
        height: '100%',
        width: '100%,',
        overflow: 'hidden',
    },
    slide: {
        padding: 10,
        overflowX: 'hidden'
    },
    tabs: {
        background: '#ff4081'
    }
}

ReactDOM.render(
    <Provider store={store}>
        <App />
    </Provider>,
    document.getElementById('app')
);


