



export const changeCurrent = (song) => {
    return {
        type: 'CHANGE_CURRENT',
        song
    }
}

export const changeList = (list) => {
    return {
        type: 'CHANGE_LIST',
        list,
    }
}

export const changeIndex = (index) => {
    return {
        type: 'CHANGE_INDEX',
        index
    }
}

export const addToList = (song) => {
    return {
        type: 'ADD_TO_LIST',
        song
    }
}


