

import React, {Component} from 'react';
import {
  Table,
  TableBody,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn,
} from 'material-ui/Table';

import list from '../assert/rankList.json';

export default class RankList extends Component{
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            msg: '',
        };
    }

    handleRowSelection(index) {
        var hash = list[index].Hash;
        let url = 'http://www.kugou.com/yy/index.php?r=play/getdata&hash=' + hash;
        let message = {
            type: 'fetch_by_hash',
            url: url
        };
        window.postMessage(JSON.stringify(message));
        this.props.changeStatus(1);
        e.preventDefault();
    }

    componentDidMount() {
        window.document.addEventListener('message',  (e) => {
            const message = e.data;
            if(JSON.parse(message).type === 'fetch_by_hash') {
                this.props.changeCurrent(JSON.parse(JSON.parse(message).data));
                this.setState({
                    msg: JSON.parse(JSON.parse(message).data).song_name,
                    open: true
                });
            }
        });
    }

    render() {
        let tableRow = list.map((v, k) => {
            return (
                <TableRow key={k}>
                    <TableRowColumn>{v.FileName}</TableRowColumn>
                    <TableRowColumn>{v.timeLen}</TableRowColumn>
                </TableRow>
            )
        });
        return (
            <Table
                onRowSelection={this.handleRowSelection}
            >
                <TableHeader
                    displaySelectAll={true}
                >
                    <TableRow>
                        <TableHeaderColumn>歌名</TableHeaderColumn>
                        <TableHeaderColumn>时间</TableHeaderColumn>
                    </TableRow>
                </TableHeader>
                <TableBody 
                    displayRowCheckbox={true}
                >
                    {tableRow}
                </TableBody>
            </Table>
        )
    }
}



