

import { connect } from 'react-redux';
import Header from '../component/header';
import { changeSearchText } from '../action/header';
import { toggleSearchList, changeSearchList } from '../action/searchList';

const mapStateToProps = (state) => {
    return {
        searchText: state.searchText,
        current: state.current,
        showSearchList: state.showSearchList
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        changeSearchText: (text) => dispatch(changeSearchText(text)),
        changeSearchList: (list) => dispatch(changeSearchList(list)),
        toggleSearchList: (showSearchList) => dispatch(toggleSearchList(showSearchList))
    }
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Header);


