


export const searchList = (state = [], action) => {
    switch(action.type) {
        case 'CHANGE_SEARCH_LIST':
            return action.list;
        default: 
            return state;
    }
}

export const showSearchList = (state = true, action) => {
    switch(action.type) {
        case 'TOGGLE_SEARCH_LIST':
            return !action.showSearchList;
        default:
            return state;
    }
}


