

import React, { Component } from 'react';
import Menu from 'material-ui/Menu';
import MenuItem from 'material-ui/MenuItem';
import Snackbar from 'material-ui/Snackbar';
import {List, ListItem} from 'material-ui/List';
import IconButton from 'material-ui/IconButton';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import RaisedButton from 'material-ui/RaisedButton';

export default class PlayList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            msg: '',
        };
    }

    handleRequestClose(e) {
        this.setState({
            open: false,
        });
        e.preventDefault(e);
    };

    handleSelect(e, hash, k) {
        this.props.changeIndex(k);
        let url = 'http://www.kugou.com/yy/index.php?r=play/getdata&hash=' + hash;
        let message = {
            type: 'fetch_by_hash',
            url: url
        };
        window.postMessage(JSON.stringify(message));
        this.props.changeStatus(1);
        e.preventDefault();
    }

    closePlayList(e) {
        this.props.togglePlayList(this.props.showPlayList);
        e.preventDefault(e);
    }

    componentDidMount() {
        window.document.addEventListener('message',  (e) => {
            const message = e.data;
            if(JSON.parse(message).type === 'fetch_by_hash') {
                this.props.changeCurrent(JSON.parse(JSON.parse(message).data));
                this.setState({
                    msg: JSON.parse(JSON.parse(message).data).song_name,
                    open: true
                });
            }
        });
    }
    
    render() {
        const actions = [
            <RaisedButton 
                label="返回"
                primary={true}
                keyboardFocused={true}
                onTouchTap={this.closePlayList.bind(this)}
            />,
        ];
        let width = window.innerWidth;
        let songItem = this.props.playList.map((v, k) => {
            if ( k === this.props.index) {
                return (
                    <ListItem  
                        style={{width: width - 30}}
                        key={k} 
                        primaryText={v.FileName.split(' - ')[1]} 
                        secondaryText={v.FileName.split(' - ')[0]}
                        rightIconButton={<IconButton 
                            disabled={true}
                            iconClassName="fa fa-headphones"
                            onTouchTap={e => {
                                this.handleSelect.call(this, e, v.Hash, k);
                            }}
                        >
                        </IconButton>}
                    />
                )
            }
            return (
                <ListItem  
                    style={{width: width - 30}}
                    key={k} 
                    primaryText={v.FileName.split(' - ')[1]} 
                    secondaryText={v.FileName.split(' - ')[0]}
                    rightIconButton={<IconButton 
                        iconClassName="fa fa-headphones"
                        onTouchTap={e => {
                            this.handleSelect.call(this, e, v.Hash, k);
                        }}
                    >
                    </IconButton>}
                />
            )
        });
        return (
            <Dialog
                title="播放列表"
                modal={true}
                actions={actions}
                contentStyle={styles.dialog}
                open={this.props.showPlayList}
                autoScrollBodyContent={true}
                autoDetectWindowHeight={true}
                onRequestClose={this.closePlayList.bind(this)}
            >
                <List>
                    {songItem}
                </List>
                <Snackbar
                    open={this.state.open}
                    message={'即将播放: ' + this.state.msg}
                    autoHideDuration={4000}
                    onRequestClose={this.handleRequestClose.bind(this)}
                />
            </Dialog>
        )
    }
}

const styles = {
    dialog: {
        width: '100%',
        position: 'fixed',
        bottom: '63px',
    },
    playList: {
        
    },
    hide: {
        display: 'none'
    }
}


