

import React, { Component } from 'react';
import IconButton from 'material-ui/IconButton';

let timeThread = null;

export default class PlayControl extends Component{
    constructor(props) {
        super(props);
        this.state = {
            current: 0,
        }
    }

    handlePlay(e) {
        if (this.props.status === 0) {
            this.audio.pause();
            this.props.changeStatus(1);
        } else {
            this.audio.play();
            this.props.changeStatus(0);
        }
        e.preventDefault(e);
    }

    togglePlayList(e) {
        this.props.togglePlayList(this.props.showPlayList);
        e.preventDefault(e);
    }

    componentDidMount() {
        window.document.addEventListener('message',  (e) => {
            const message = e.data;
            if(JSON.parse(message).type === 'fetch_by_hash') {
                this.props.changeCurrent(JSON.parse(JSON.parse(message).data));
            }
        });

        this.audio.onloadedmetadata = () => {
            this.audio.play();
            this.props.changeStatus(0);
            let total = parseInt(this.props.current.timelength/1000);
            let time = 0;
            if (timeThread !== null) {
                clearInterval(timeThread);
            }
            timeThread = setInterval(() => {
                if (time === total) {
                    clearInterval(timeThread);
                    let next = this.props.index + 1;
                    this.props.changeIndex(next);
                    let url = 'http://www.kugou.com/yy/index.php?r=play/getdata&hash=' + this.props.playList[next].Hash;
                    let message = {
                        type: 'fetch_by_hash',
                        url: url
                    };
                    window.postMessage(JSON.stringify(message));
                    this.props.changeStatus(1);
                    this.setState({
                        current: 0
                    });
                    e.preventDefault();
                } else {
                    this.setState({
                        current: time
                    });
                }
                time++;
            }, 1000);
        };
    }

    render() {
        return (
            <section style={styles.control}>
                <div style={styles.imgCont}>
                    <img style={styles.img} src={this.props.current.img}/>
                </div>
                <div style={styles.songDetail}>
                    <span style={styles.songName}>{this.props.current.song_name}</span>
                    <span style={styles.authorName}>{this.props.current.author_name + ' - ' + this.state.current + ' : ' + parseInt(this.props.current.timelength/1000) || 0}</span>
                </div>
                <div style={styles.controls}>
                    <IconButton 
                        style={styles.button}
                        iconStyle={styles.icon}
                        iconClassName={this.props.status === 0 ? 'fa fa-pause-circle-o' : 'fa fa-play-circle'}
                        onTouchTap={this.handlePlay.bind(this)}
                    >
                    </IconButton>
                    <IconButton 
                        iconStyle={styles.icon} 
                        iconClassName="fa fa-list-ul"
                        onTouchTap={this.togglePlayList.bind(this)}
                    >
                    </IconButton>
                </div>
                <audio ref={audio => this.audio = audio} src={this.props.current.play_url}></audio>
            </section>
        )
    }
}

const styles = {
    control: {
        width: '100%',
        height: 50,
        position: 'fixed',
        bottom: 0,
        background: '#ff4081',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        color: '#fff',
    },
    imgCont: {
        flex: 1,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
    },
    songDetail: {
        flex: 4,
        display: 'flex',
        flexDirection: 'column',
    },
    controls: {
        flex: 2,
        display: 'flex',

    },
    img: {
        height: '40px',
        width: '40px'
    },
    icon: {
        color: '#fff',
    },
    songName: {
        fontWeight: 'bold',
        fontSize: '15px',
    },
    authorName: {
        fontSize: '12px',
    },
}


