

import React, { Component } from 'react';

export default class Author extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        setTimeout( () => {
            this.props.changeAuthor('Liang Ren Hong');
        }, 5000);
    }

    render() {
        return (
            <section>
                作者: {this.props.author}
            </section>
        )
    }
}


