

import React, { Component } from 'react';
import Menu from 'material-ui/Menu';
import MenuItem from 'material-ui/MenuItem';
import Snackbar from 'material-ui/Snackbar';
import {List, ListItem} from 'material-ui/List';
import IconButton from 'material-ui/IconButton';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import RaisedButton from 'material-ui/RaisedButton';

export default class SearchList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            msg: ''
        }
    }

    componentDidMount() {
        
    }

    addToPlayList(e, v) {
        this.props.addToList(v);
        this.setState({
            msg: v.FileName.split(' - ')[1],
            open: true
        });
    }

    closeSearchList() {
        this.props.toggleSearchList(this.props.showSearchList);
    }

    handleRequestClose(e) {
        this.setState({
            open: false,
        });
        e.preventDefault(e);
    };
    
    render() {
        const actions = [
            <RaisedButton 
                label="返回"
                primary={true}
                keyboardFocused={true}
                onTouchTap={this.closeSearchList.bind(this)}
            />,
        ];
        let width = window.innerWidth;
        let songItem = this.props.searchList.map((v, k) => {
            return (
                <ListItem  
                    style={{width: width - 30}}
                    key={k} 
                    primaryText={v.FileName.split(' - ')[1]} 
                    secondaryText={v.FileName.split(' - ')[0]}
                    rightIconButton={<IconButton 
                        iconClassName="fa fa-plus"
                        onTouchTap={e => {
                            this.addToPlayList.call(this, e, v);
                        }}
                    >
                    </IconButton>}
                />
            )
        });
        return (
            <Dialog
                title="搜索结果"
                modal={true}
                actions={actions}
                contentStyle={styles.dialog}
                open={this.props.showSearchList}
                autoScrollBodyContent={true}
                autoDetectWindowHeight={true}
                onRequestClose={this.closeSearchList.bind(this)}
            >
                <List>
                    {songItem}
                </List>
                <Snackbar
                    open={this.state.open}
                    message={'<' + this.state.msg + '> 已经添加到播放列表'}
                    autoHideDuration={4000}
                    onRequestClose={this.handleRequestClose.bind(this)}
                />
            </Dialog>
        )
    }
}

const styles = {
    dialog: {
        width: '100%',
        position: 'fixed',
        bottom: '63px',
    },
    playList: {
        
    },
    hide: {
        display: 'none'
    }
}


